<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <span class="text-h5">BMI 計算機</span>
    </v-app-bar>

    <v-main>
      <v-container class="my-5">
        <!-- 身長入力欄 -->
        <v-row no-gutters>
          <v-col cols="auto">
            <v-subheader>身長</v-subheader>
          </v-col>
          <v-col cols="auto">
            <v-text-field
              v-model="height"
              dense
              hide-details
              outlined
              style="width: 250px"
              suffix="cm"
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>

        <!-- 体重入力欄 -->
        <v-row no-gutters>
          <v-col cols="auto">
            <v-subheader>体重</v-subheader>
          </v-col>
          <v-col cols="auto">
            <v-text-field
              v-model="weight"
              dense
              hide-details
              outlined
              style="width: 250px"
              suffix="kg"
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>

        <!-- 結果表示欄 -->
        <v-row no-gutters>
          <v-col cols="auto">
            <v-alert
              v-if="success"
              dense
              style="width: 500px"
              text
              type="success"
            >
              {{ this.message }}
            </v-alert>
            <v-alert
              v-else-if="success === false"
              dense
              style="width: 500px"
              text
              type="error"
            >
              {{ this.message }}
            </v-alert>
          </v-col>
        </v-row>

        <!-- 計算ボタン -->
        <v-row no-gutters>
          <v-col>
            <v-btn
              color="primary"
              @click="calculate"
              :disabled="!height || !weight"
              >計算する</v-btn
            >
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>
<script>
export default {
  name: "App",

  data: () => ({
    height: null,
    weight: null,
    success: null,
    message: null,
  }),

  methods: {
    calculate() {
      if (!this.height || !this.weight) {
        return; // 身長または体重が未入力の場合
      }

      this.success = null;
      this.message = null;

      const req = JSON.stringify({
        height: this.height,
        weight: this.weight,
      });

      this.axios
        .post(`http://127.0.0.1:5000/api/get_bmi`, req)
        .then((res) => {
          this.success = res.data.success;

          if (res.data.success) {
            // 正常終了した場合
            const bmi = res.data.bmi.toFixed(1);
             const normalWeight = res.data.normal_weight.toFixed(1);
            this.message = `BMI は${bmi}、標準体重は${normalWeight}kgです。`;
          } else {
            // 異常終了した場合
            this.message = res.data.message;
          }
        })
        .catch(() => {
           // 通信に失敗した場合
          this.success = false;
          this.message = "通信中にエラーが発生しました。";
        });
    },
  },
};
</script>